import React, { useEffect, useState } from "react";
import { ScrollView, Text, StyleSheet, View, Pressable, ImageBackground } from "react-native";
import { SafeAreaView } from "react-native-safe-area-context";
import { useRoute } from "@react-navigation/native";
import { useUniversalNavigation, SCREENS } from "../navigation";
import TopBar from "../components/TopBar";
import QuestionOption from "../components/QuestionOption";
import NavBar from "../components/NavBar";
import Item from "../components/Item";
import Klipartz from "../assets/Klipartz.svg";
import { Color, FontSize, FontFamily } from "../GlobalStyles";
import { Pregunta } from "../data/types";
import { useTranslation } from "../localization";

// Define los tipos para los parÃ¡metros de la ruta
type RouteParamList = {
  Revision: { 
    plazaId: string;
    respuestas: { [key: string]: string };
    preguntas: Pregunta[];
    modo: string;
    language?: string; // El idioma elegido en la pantalla anterior (opcional)
  };
};

import { RouteProp } from "@react-navigation/native";
type RevisionScreenRouteProp = RouteProp<RouteParamList, 'Revision'>;

const Revision = () => {
  const route = useRoute<RevisionScreenRouteProp>();
  const navigation = useUniversalNavigation();
  const { language: contextLanguage, t } = useTranslation();
  const { plazaId, respuestas, preguntas, modo, language: routeLanguage } = route.params || { 
    plazaId: '', 
    respuestas: {}, 
    preguntas: [],
    modo: 'revision'
  };
  
  // Si se pasÃ³ un idioma en la ruta, lo usamos, de lo contrario usamos el del contexto
  const language = routeLanguage || contextLanguage;
  
  // Calcular puntuaciÃ³n
  const calcularPuntuacion = () => {
    let puntos = 0;
    preguntas.forEach(pregunta => {
      const respuestaUsuarioIndex = respuestas[pregunta.id];
      
      if (respuestaUsuarioIndex && pregunta.opciones[Number(respuestaUsuarioIndex)]?.esCorrecta) {
        puntos++;
      }
    });
    return puntos;
  };
  
  const puntuacion = calcularPuntuacion();
  const porcentajeAciertos = (puntuacion / preguntas.length) * 100;
  
  // Volver al menÃº de la plaza
  const volverAlMenu = () => {
    navigation.navigate(SCREENS.MENU_PLAZA, { plazaId });
  };

  return (
    <View style={styles.backgroundContainer}>
      <View style={styles.container}>
        <TopBar text="Resultados" textoWidth={142} translationKey="results.title" />
        
        <View style={styles.puntuacionContainer}>
          <Text style={styles.puntuacionText}>
            {language === 'es' 
              ? `Tu puntuaciÃ³n: ${puntuacion} de ${preguntas.length} (${porcentajeAciertos.toFixed(0)}%)`
              : `Your score: ${puntuacion} of ${preguntas.length} (${porcentajeAciertos.toFixed(0)}%)`
            }
          </Text>
        </View>
        
        <ScrollView
          style={styles.scrollView}
          contentContainerStyle={styles.scrollViewContent}
        >
          {preguntas.map((pregunta, preguntaIndex) => {
            return (
              <View key={preguntaIndex} style={styles.preguntaContainer}>
                <Text style={styles.preguntaText}>
                  {preguntaIndex + 1}. {pregunta.texto[language as keyof typeof pregunta.texto]}
                </Text>
                
                <View style={styles.opcionesContainer}>
                  {pregunta.opciones.map((opcion, opcionIndex) => {
                    // Determinar el estado del cÃ­rculo para cada opciÃ³n
                    let estado: "SinMarcar" | "Seleccionado" | "Correcto" | "Incorrecto" = "SinMarcar";
                    
                    const esRespuestaUsuario = respuestas[pregunta.id] === String(opcionIndex);
                    const esCorrecta = opcion.esCorrecta;
                    
                    if (modo === 'revision') {
                      // En modo revisiÃ³n
                      if (esRespuestaUsuario && esCorrecta) {
                        estado = "Correcto";
                      } else if (esRespuestaUsuario && !esCorrecta) {
                        estado = "Incorrecto";
                      } else if (esCorrecta) {
                        estado = "Correcto"; // Mostrar la correcta aunque no la haya elegido
                      }
                    } else {
                      // En modo normal de preguntas
                      if (esRespuestaUsuario) {
                        estado = "Seleccionado";
                      }
                    }
                    
                    return (
                      <QuestionOption
                        key={opcionIndex}
                        text={opcion.texto[language as keyof typeof opcion.texto]}
                        state={estado}
                        onPress={() => {}} // No hacer nada en modo revisiÃ³n
                        disabled={modo === 'revision'}
                        explanation={estado === "Correcto" && pregunta.explicacion ? pregunta.explicacion[language as keyof typeof pregunta.explicacion] : ""}
                        showExplanation={modo === 'revision' && estado === "Correcto"}
                      />
                    );
                  })}
                </View>
              </View>
            );
          })}
        </ScrollView>
        
        <View style={styles.buttonContainer}>
          <Item 
            text={t("back.to.menu")}
            onPress={volverAlMenu}
            width="90%"
            height={70}
            textSize={24}
          />
        </View>
        
        <NavBar klipartz={<Klipartz width={60} height={60} />} />
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  backgroundContainer: {
    flex: 1,
    width: "100%",
    height: "100%",
    backgroundColor: Color.colorGray200, // Fondo azul oscuro
  },
  container: {
    flex: 1,
    backgroundColor: Color.colorGray200,
    width: "100%",
    height: "100%",
    overflow: "hidden",
  },
  viewBg: {
    backgroundColor: Color.colorGray200,
    flex: 1,
  },
  view: {
    width: "100%",
    height: "100%",
    overflow: "hidden",
  },
  puntuacionContainer: {
    padding: 20,
    alignItems: "center",
    justifyContent: "center",
    marginTop: 10,
  },
  puntuacionText: {
    fontSize: FontSize.size_32,
    color: Color.colorWhite,
    fontFamily: FontFamily.interBold,
    fontWeight: "700",
    textAlign: "center",
  },
  scrollView: {
    flex: 1,
    width: "100%",
  },
  scrollViewContent: {
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "flex-start",
    paddingBottom: 20,
  },
  preguntaContainer: {
    width: '90%',
    maxWidth: 500,
    backgroundColor: 'rgba(16, 102, 138, 0.3)', // Color.colorSteelblue con transparencia
    borderRadius: 10,
    padding: 15,
    marginVertical: 10,
  },
  preguntaText: {
    fontSize: FontSize.size_24,
    fontFamily: FontFamily.interBold,
    color: Color.colorWhite,
    marginBottom: 15,
  },
  opcionesContainer: {
    width: "100%",
  },
  buttonContainer: {
    width: "100%",
    alignItems: "center",
    paddingVertical: 20,
    marginBottom: 120, // Aumentado de 80 a 120 para mover el botÃ³n mÃ¡s abajo
  },
  // Mantenemos algunos estilos por compatibilidad
  respuestas: {
    alignSelf: "stretch",
    maxWidth: "100%",
    flex: 1,
  },
  respuestasContainerContent: {
    flexDirection: "column",
    paddingHorizontal: 15,
    paddingVertical: 0,
    alignItems: "center",
    justifyContent: "flex-start",
    paddingBottom: 20,
  },
  respuestaContainer: {
    flexDirection: "row",
    alignItems: "center",
    marginBottom: 10,
  },
  respuestaLabel: {
    fontSize: FontSize.size_20,
    fontFamily: FontFamily.interRegular,
    color: Color.colorWhite,
    marginRight: 10,
    minWidth: 150,
  },
  respuestaText: {
    fontSize: FontSize.size_20,
    fontFamily: FontFamily.interBold,
    flex: 1,
  },
  respuestaCorrecta: {
    color: '#4CAF50',
  },
  respuestaIncorrecta: {
    color: '#F44336',
  },
  explicacionText: {
    fontSize: FontSize.size_20,
    fontFamily: FontFamily.interRegular,
    color: Color.colorWhite,
    marginTop: 10,
    fontStyle: 'italic',
  },
  volverBtnContainer: {
    alignItems: "center",
    justifyContent: "center",
    marginVertical: 20,
    marginBottom: 95,
  },
});

export default Revision;
