import * as React from "react";
import { Text, StyleSheet, View, Pressable } from "react-native";
import { FontSize, FontFamily, Color } from "../GlobalStyles";
import UniversalEmoji from "./UniversalEmoji";

export type EmojisType = {
  /** Emoji a mostrar */
  emoji?: string;
  /** TamaÃ±o del emoji, si no se especifica usa el tamaÃ±o por defecto */
  size?: number;
};

const Emojis = ({ emoji = "ð²", size }: EmojisType) => {
  // Ajuste vertical para el emoji
  // Si necesitas subirlo: usa valores negativos (ej: -2, -5)
  // Si necesitas bajarlo: usa valores positivos (ej: 2, 5)
  const ajusteVertical = 2; // <-- MODIFICA ESTE VALOR para ajustar la posiciÃ³n

  return (
    <View style={styles.emojis}>
      <View style={{ marginTop: ajusteVertical }}>
        <UniversalEmoji 
          emoji={emoji} 
          size={size || 72} 
          isFlag={/[\u{1F1E0}-\u{1F1FF}]/gu.test(emoji)}
        />
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  emojis: {
    overflow: "visible",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "center",
    alignContent: "center",
    width: "100%",
    height: "100%", // Aseguramos que ocupe todo el alto disponible
  },
  text: {
    fontSize: 72, // TamaÃ±o personalizado mucho mÃ¡s grande que el predefinido
    fontWeight: "700",
    fontFamily: FontFamily.interBold,
    color: Color.colorWhite,
    textAlign: "center",
    includeFontPadding: false, // Elimina el padding extra que a veces aÃ±aden algunos sistemas
    textAlignVertical: 'center', // Centra verticalmente el texto (para Android)
    // No incluimos marginTop aquÃ­ para controlarlo desde la variable ajusteVertical
  },
});

export default Emojis;
